import React, { useEffect, useMemo, useState } from "react";
import $ from 'jquery';
import { Connection, PublicKey } from "@solana/web3.js";
import {
  getOrCreateAssociatedTokenAccount,
  getAccount,
  getMint,
} from "@solana/spl-token";
import {
  WalletAdapterNetwork,
  WalletProvider,
  ConnectionProvider,
  useWallet,
} from "@solana/wallet-adapter-react";
import {
  WalletModalProvider,
  WalletMultiButton,
} from "@solana/wallet-adapter-react-ui";
import {
  UnsafeBurnerWalletAdapter,
  PhantomWalletAdapter,
  SolflareWalletAdapter,
  TrustWalletAdapter,
  TorusWalletAdapter
} from '@solana/wallet-adapter-wallets';

// Default styles that can be overridden by your app
require("@solana/wallet-adapter-react-ui/styles.css");

const ECB_MINT_ADDRESS = "9LZe5ioBH4zBZ3F975qrGvJaehizauGJLR9cZaDytdzP";
const CO2_MINT_ADDRESS = "88B8fmC56ejpGH26RwyZ5pVuxDBPhuZA3D2bM8192WKx";

function TokenBalance() {
  const [ECBbalance, setECBBalance] = useState(null);
  const [CO2balance, setCO2Balance] = useState(null);
  const [walletAddress, setWalletAddress] = useState(null);
  const [totalEcbSupply, setTotalEcbSupply] = useState(null);
  const [totalCo2Supply, setTotalCo2Supply] = useState(null);
  
  const { publicKey } = useWallet();
  useEffect(() => {
    if (publicKey) {
      setWalletAddress(publicKey);
    }
  }, [publicKey]);

  useEffect(() => {
    const fetchBalance = async (ecb_mint, co2_mint) => {
      if (walletAddress) {
        const connection = new Connection(
          "https://api.devnet.solana.com",
          "confirmed"
        );
        const ecbMintAddress = new PublicKey(ecb_mint);
        const co2MintAddress = new PublicKey(co2_mint);
        const ECBAccount = await getOrCreateAssociatedTokenAccount(
          connection,
          walletAddress,
          ecbMintAddress,
          walletAddress
        );
        const CO2Account = await getOrCreateAssociatedTokenAccount(
          connection,
          walletAddress,
          co2MintAddress,
          walletAddress
        );
        const ecbAccountInfo = await getAccount(connection, ECBAccount.address);
        const co2AccountInfo = await getAccount(connection, CO2Account.address);
        setECBBalance(
          (parseFloat(ecbAccountInfo.amount.toString()) / 10e7).toFixed(2)
        );
        setCO2Balance(
          (parseFloat(co2AccountInfo.amount.toString()) / 10e8).toFixed(2)
        );
        // Fetch the total supply of the token
        const tokenMintAddress = new PublicKey(ecb_mint);
        const mintInfo = await getMint(connection, tokenMintAddress);
        setTotalEcbSupply(parseFloat(mintInfo.supply.toString() / 10e7).toFixed(2));

        // Fetch the total supply of the token
        const co2Address = new PublicKey(co2_mint);
        const co2Info = await getMint(connection, co2Address);
        setTotalCo2Supply(parseFloat(co2Info.supply.toString() / 10e8).toFixed(2));  

      }      
    };
    fetchBalance(ECB_MINT_ADDRESS, CO2_MINT_ADDRESS);
  }, [walletAddress]);

  return (
    
    <div>
      <WalletMultiButton />
      
      {walletAddress ? (
        <div class="root_inner">
          <p class='wallet_address'>{walletAddress.toBase58()}</p>
          <p class='ecb_balance'>{ECBbalance !== null ? ECBbalance : "Loading..."}</p>
          <p class='ecb_total_supply'>{totalEcbSupply !== null ? totalEcbSupply : "Loading..."}</p>
          <p class='co2t_balance'>{CO2balance !== null ? CO2balance : "Loading..."}</p>
          <p class='co2t_total_supply'>{totalCo2Supply !== null ? totalCo2Supply : "Loading..."}</p>
        </div>
      ) : (
        <p></p>
      )}
    </div>
  );
}

function App() {
      // Supported wallets (add/remove as needed)
      const wallets = useMemo(
        () => [
          new PhantomWalletAdapter(),
          new SolflareWalletAdapter(),
          new UnsafeBurnerWalletAdapter(),
          new TrustWalletAdapter(),
          new TorusWalletAdapter(),
        ],
        []
      );
      
  return (
    <ConnectionProvider endpoint="https://api.devnet.solana.com">
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <TokenBalance />
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

export default App;
